<template>
	<div>
		<template>
			<!---->
			<el-upload class="upload-demo" :show-file-list=false action="" drag :before-upload="acceptFile">
			  <i class="el-icon-upload"></i>
			  <div class="el-upload__text"><em></em></div>
			</el-upload>
		</template>
		<template>
			<!--excel-->
			<span></span>
			<div>
				<el-table :data="listTable" height="400" style="width: 100%" border>
					<el-table-column label="" align="center" prop="yearOfAward" ></el-table-column>
				 	<el-table-column label="" align="center" prop="name" ></el-table-column>
				 	<el-table-column label="" align="center" prop="yearOfBirth" ></el-table-column>
				 	<el-table-column label="" align="center" prop="sex" ></el-table-column>
				 	<el-table-column label="" align="center" prop="companyName" ></el-table-column>
				 	<el-table-column label="" align="center" prop="companyAddress" ></el-table-column>
				 	<el-table-column label="" align="center" prop="address_X" ></el-table-column>
					<el-table-column label="" align="center" prop="address_Y" ></el-table-column>
					<el-table-column label="" align="center" prop="major" ></el-table-column>
					<el-table-column label="" align="center" prop="yearOfGraduate" ></el-table-column>
				</el-table>
				<el-pagination style="padding: 5px 0px;font-size:24px;"
			    :total.sync="totalCount"
					layout="total">
			  </el-pagination>
			</div>

		</template>
	</div>
</template>

<script>
import XLSX from 'xlsx';
import { uploadMock } from '../../api/api';
export default {
	name: 'app',
	data(){
		return{
			listTable:[], //excel
			currentPage:1,
			totalCount:0
		}
	},
	methods:{
		handleSizeChange(val) {
        console.log(` ${val} `);
    },
    handleCurrentChange(val) {
      console.log(`: ${val}`);
    },
		acceptFile(f){
			//"."
			let extStart = f.name.lastIndexOf(".");
			// 
			let filetype = f.name.substring(extStart, f.name.length).toUpperCase();
			// excel 
			if (filetype != ".XLSX" && filetype != ".XLS" ){
			alert("");
			return;
		}
			// 
			this.handleFile(f);
		},
		handleFile(f){
			if(window.FileReader) {
				// fileReader
				var  reader = new FileReader();
			}else{
				this.$message.error('fileReader,');
			}
			reader.onload =(e)=>{
				let data = e.target.result,
					//XLSL 
					workbook = XLSX.read(data, { type: 'binary' }),
					//excelsheet
					sheetName = workbook.SheetNames[0],
					//sheet
						sheet = workbook.Sheets[sheetName];
						//
						this.listTable=[];
						//excel
						this.getData(sheet);
			};
			//
			reader.readAsBinaryString(f);
		},
		getData(sheet){
			let colArr=[], //A~Z
				yearOfAward="",
				name="",
				yearOfBirth="",
				sex="",
				companyName="",
				companyAddress="",
				address_X="",
				address_Y="",
				major="",
				yearOfGraduate="";
		  //A~Z
			for (let col = 65; col <= 90; col++) {
				let c = String.fromCharCode(col);
				colArr.push(c);
			}
			//A1Z1   
			for(var i=0;;i++){
				if(sheet[colArr[i]+1]==null){
					break;
				}
				switch (sheet[colArr[i]+1].v){
					case "":
						yearOfAward=colArr[i];
						break;
					case "":
						name=colArr[i];
						break;
					case "":
						yearOfBirth=colArr[i];
						break;
					case "":
						sex=colArr[i];
						break;
					case "":
						companyName=colArr[i];
						break;
					case "":
						companyAddress=colArr[i];
						break;
					case "X":
						address_X=colArr[i];
						break;
					case "Y":
						address_Y=colArr[i];
						break;
					case "":
						major=colArr[i];
						break;
					case "":
						yearOfGraduate=colArr[i];
						break;
					default:
						break;
				}
			}
			//
			for (var row=2; ;row++) {
				  let obj={};
					// 
					if(sheet[yearOfAward+row]==null&&sheet[name+row]==null&&sheet[yearOfBirth+row]==null&&sheet[sex+row]==null&&sheet[companyName+row]==null&&sheet[companyAddress+row]==null
					&&sheet[address_X+row]==null&&sheet[address_Y+row]==null&&sheet[major+row]==null&&sheet[yearOfGraduate+row]==null){
				    break;
		 	    }
					obj.yearOfAward=sheet[yearOfAward+row].v;
					obj.name=sheet[name+row].v;
					obj.yearOfBirth=sheet[yearOfBirth+row].v;
					obj.sex=sheet[sex+row].v;
					obj.companyName=sheet[companyName+row].v;
					obj.companyAddress=sheet[companyAddress+row].v;
					obj.address_X=sheet[address_X+row].v;
					obj.address_Y=sheet[address_Y+row].v;
					obj.major=sheet[major+row].v;
					obj.yearOfGraduate=sheet[yearOfGraduate+row].v;
					// 
					this.listTable.push(obj);
			}
			this.totalCount = row-2;
			this.upload();
		},

		//Mock
		upload: function () {
			let para = {
				obj: this.listTable
			};
			this.loading = true;
			uploadMock(para).then((res) => {
				this.code = res.data.code;
				this.loading = false;
				if (this.code == 200) {
					this.$alert('', '', {
	          confirmButtonText: '',
	          callback: action => {
							console.log(para);
	          }
	        });
				}
			});
		}
	}
}
</script>



